@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@300;500;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Piazzolla:ital,wght@0,100;0,400;0,500;0,700;0,900;1,100;1,200&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loader-container{width:100vw}.loader-container .loader{font-size:10px;margin:50px auto;text-indent:-9999em;width:11em;height:11em;border-radius:50%;background:#333;background:linear-gradient(to right, #333 10%, rgba(255,255,255,0) 42%);position:relative;animation:load3 1.4s infinite linear;transform:translateZ(0)}.loader-container .loader:before{width:50%;height:50%;background:#333;border-radius:100% 0 0 0;position:absolute;top:0;left:0;content:''}.loader-container .loader:after{background:#b20000;width:75%;height:75%;border-radius:50%;content:'';margin:auto;position:absolute;top:0;left:0;bottom:0;right:0}@keyframes load3{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.articles-container{margin:0 auto;display:flex;flex-direction:column;align-items:center;background-image:linear-gradient(#ddd, #bbb);transition:1s}.articles-container .articles{width:70%;margin-top:-200px;display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:auto;margin-bottom:100px;grid-gap:25px;gap:25px}.articles-container .articles a{text-decoration:none;color:#aaaaaa;width:300px;height:600px;animation:enlarge-new-item 300ms ease-in-out 0s 1 normal forwards;box-sizing:border-box !important}.articles-container .articles a .article{margin:0 auto;background-color:#ffffff;max-width:300px;height:600px;display:flex;flex-direction:column;justify-content:space-between;transform:scale(1);transition:transform 0.2s ease-in-out}.articles-container .articles a .article .article-image-div{position:relative;max-width:300px;height:300px;overflow:hidden}.articles-container .articles a .article .article-image-div img{position:absolute;min-height:299px;height:100%;left:50%;top:50%;transform:translateX(-50%) translateY(-50%);opacity:0.9}.articles-container .articles a .article .text-container{padding:25px;padding-top:20px;padding-bottom:40px;height:300px;display:flex;flex-direction:column;justify-content:space-between;position:relative}.articles-container .articles a .article .text-container h1{font-size:20px}.articles-container .articles a .article .text-container h2{font-style:italic;font-weight:200;font-size:14px}.articles-container .articles a .article .text-container .arrow{position:absolute;width:40px;height:40px;transform:rotate(45deg);bottom:-20px;background:white;z-index:1}.articles-container .articles a .article:hover{color:#333333;box-shadow:5px 5px 10px 5px rgba(0,0,0,0.05);border:1px solid #333333;transform:scale(1.05)}.articles-container .articles a .article:hover .article .article-image-div img{opacity:1}@keyframes enlarge-new-item{from{transform:scale(0)}to{transform:scale(1)}}

.Header{width:100vw;height:80vh;background-image:url(/oko-articles-react/static/media/reader.8b6ca600.jpg);background-size:cover;background-position-y:50%}.Header .header-texts{width:70vw;margin:0 auto;padding-top:100px;color:#ffffff}.Header .header-texts h1{font-size:100px;margin:0}.Header .header-texts h2{font-size:35px;margin:0}

html{box-sizing:border-box}*,*:before,*:after{margin:0;padding:0;box-sizing:inherit;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.App{font-family:'Piazzolla', serif;width:100%;display:flex;flex-direction:column;align-items:center}

