
.articles-container {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: linear-gradient( #dddddd, #bbbbbb);
    transition: 1s;
    .articles {
        width: 70%;
        margin-top: -200px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: auto;
        margin-bottom: 100px;
        gap: 25px;
        a {
            text-decoration: none;
            color: #aaaaaa;
            // border: 5px solid red
            width: 300px;
            height: 600px;
            animation: enlarge-new-item 300ms ease-in-out 0s 1 normal forwards;
            box-sizing: border-box !important;
            .article {
                margin: 0 auto;
                background-color: #ffffff;
                max-width: 300px;
                height: 600px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                transform: scale(1);
                transition: transform 0.2s ease-in-out;

                .article-image-div {
                    position: relative;
                    max-width: 300px;
                    height: 300px;
                    overflow: hidden;
                    img {
                        position: absolute;
                        min-height: 299px;
                        // min-width: 300px
                        height: 100%;
                        left: 50%;
                        top: 50%;
                        transform: translateX(-50%) translateY(-50%);
                        opacity: 0.9; } }
                .text-container {
                    padding: 25px;
                    padding-top: 20px;
                    padding-bottom: 40px;
                    height: 300px;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    position: relative;
                    h1 {
                        font-size: 20px; }
                    h2 {
                        font-style: italic;
                        font-weight: 200;
                        font-size: 14px;
 }                        // margin: 45px
                    .arrow {
                        position: absolute;
                        width: 40px;
                        height: 40px;
                        transform: rotate(45deg);
                        bottom: -20px;
                        background: white;
                        z-index: 1; } }
                &:hover {
                    color: #333333;
                    box-shadow: 5px 5px 10px 5px rgba(0,0,0,0.05);
                    border: 1px solid #333333;
                    transform: scale(1.05);
                    .article {
                        .article-image-div {
                            img {
                                opacity: 1; } } } } } } } }

@keyframes enlarge-new-item {
    from {
        transform: scale(0); }

    to {
        transform: scale(1); } }
